.StudentEntryUI {
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 5px;
    font-family: Quicksand;
    position: relative;
}

.StudentEntryUI .StudentEntryUI-Content {
    width: 0;
    min-height: 50vh;
    background-color: #135bcc;
    animation: fullWidth 1s linear 1 forwards alternate;
    padding: 30px 50px;
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    display: flex;
    justify-content: flex-end;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm {
    width: 40%;
    height: 70vh;
    padding: 10vh 20px;
    border-radius: 5px;
    background-color: #fff;
    position: absolute;
    top: -10vh;
    left: 10%;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    color: #135bcc;
    text-align: center;
    overflow: hidden;
    transform: scale(0.8);
    opacity: 0;
    animation: popup .2s linear 1.5s 1 forwards alternate;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .StepForms {
    opacity: 0;
    transform: scale(0);
    display: none;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .StepForms .PrevStep {
    cursor: pointer;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .ShowStepForms {
    animation: popup .3s linear 1 forwards alternate;
    display: block;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .StepForms #StudentInfoMsg {
    font-size: 13px;
    margin-top: 20px;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm hr {
    height: 1px !important;
    background-color: rgb(36, 116, 193, .8);
    margin: 3px auto;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm hr:nth-child(2) {
    width: 50%;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm hr:nth-child(3) {
    width: 30%;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm hr:nth-child(4) {
    width: 10%;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .inputs {
    width: 100%;
    border: 0;
    border-bottom: 1px solid lightgray;
    font-size: 12px;
    padding: 10px;
    margin-bottom: 15px;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .inputs:focus {
    outline: none;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .btns {
    background-color: #135bcc;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    color: #fff;
    width: 100%;
    border: 0;
    padding: 8px;
    margin-top: 15px;
    margin-bottom: 15px;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm .btns:focus {
    outline: none;
}

.StudentEntryUI .StudentEntryUI-Content .EntryForm:focus {
    outline: none;
}

.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps {
    display: flex;
    /* align-items: center; */
    justify-content: center;
}

.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .Step {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    color: #135bcc;
    font-weight: bold;
    font-size: 18px;
    border-radius: 50%;
    padding: 3px;
    font-family: Poppins;
}

.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .Step .Step-inner i,
.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .Step .Step-inner span {
    width: 42px;
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .Step .Step-inner i {
    background-color: #135bcc;
    color: #fff;
    animation: showIcon .1s linear 1 forwards alternate;
    transform: scale( 0 );
}

@keyframes showIcon {
    100% {
        transform: scale( 1 );
    }
}

.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .line {
    height: 3px;
    width: 50px;
    background-color: #fff;
    margin-top: 21px;
}

.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .line:last-child {
    display: none;
}

.StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps small {
    text-align: center;
    display: block;
    color: #fff;
    font-size: 10px;
}

@media screen and (max-width: 1200px) {
    .StudentEntryUI .StudentEntryUI-Content {
        animation: fullWidth1200 1s linear 1 forwards alternate;
    }
}
 
@media screen and (max-width: 992px) {
    .StudentEntryUI .StudentEntryUI-Content {
        animation: fullWidth992 1s linear 1 forwards alternate;
    }
}

@media screen and (max-width: 768px) {
    .StudentEntryUI .StudentEntryUI-Content {
        min-height: 50vh !important;
    }
    .StudentEntryUI .StudentEntryUI-Content .EntryForm {
        width: 90% !important;
        /* height: 60vh; */
        padding: 50px 20px;
        /* top: 15vh; */
        right: 5% !important;
        left: unset;
    }
    .StudentEntryUI .StudentEntryUI-Content .Login-Welcome-Content {
        height: 90vh;
        width: 100%;
        position: fixed;
        top: 5vh;
    }
    
    .StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps {
        color: #135bcc !important;
        animation: fullWidth768 1s 1.5s linear 1 forwards alternate;
        position: fixed;
        top: 10%;
        left: 0;
        width: 100%;
    }
    .StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .Step {
        background-color: #135bcc;
        color: #fff;
    }

    .StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .Step .Step-inner i {
        background-color: #fff;
        color: #135bcc;
    }

    .StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps .line {
        background-color: #135bcc;
    }

    .StudentEntryUI .StudentEntryUI-Content .StudentEntryUI-inner .Steps small {
        color: #135bcc;
    }
}

@media screen and (max-width: 600px) {
    .StudentEntryUI .StudentEntryUI-Content {
        padding: unset 0;
    }
}